  width: 100vw;
  height: 100vh;
  font-family: 'Roboto', sans-serif;
}

.appContent {
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100%;
  position: relative;
  padding: 8px 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.MuiSvgIcon-root {
  color: rgba(0,0,0,0.8);
  cursor: pointer;
}

.MuiSvgIcon-root:hover {
  color: black;
}

